@import "./common/colours.scss";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Sora', sans-serif;
    color: $font-primary;
}

a:link, a:visited, a:hover, a:active {
    text-decoration: none;
}

body {
    background-color: $primary-colour;
}

.container {
    margin: 0rem 2.5rem;
}