@import "../../common/colours.scss";

.movie-list, .show-list {
  margin: 1.5rem 0rem;
  h2 {
    color: $font-secondary;
    margin-bottom: 1rem;
    font-weight: 600;
  }
}

.movie-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(14rem, 1fr));
  grid-gap: 1rem;
}
